



























































import { Component, Vue, Prop, Watch } from 'vue-property-decorator';
import { validationMixin } from 'vuelidate';
import Toast from '@/components/utilities/Toast.vue';
import InputFile from '@/components/utilities/InputFile.vue';
import SvgIcon from '@/components/utilities/SvgIcon.vue'
import { verifLevels } from '@/store/namespaces';
import VerifLevelsMethods from '@/store/verificationLevels/methods/verificationLevels.methods';
import { Answer, Question } from '@/modules/verificationLevels/interfaces/questions.interface';
import {ReqLevelTwo} from '@/modules/verificationLevels/interfaces/requests.interface';
import { required, minLength, helpers } from 'vuelidate/lib/validators';
import { showToast } from '@/utils/global-functions';
import Loader from '@/components/utilities/Loader.vue';

@Component({
    components: {
        Toast,
        InputFile,
        SvgIcon,
        Loader
    },
    mixins: [validationMixin],
    validations:{
        localRequirements:{
            // monthly_amount:{
            //     value:{required,minLength: minLength(1)}
            // }, Fix this
            account_uses:{
                value:{required,minLength: minLength(1)}
            } 
        }
    }
})
export default class SecondStep extends Vue {  
    @Prop() requirements!: ReqLevelTwo; //tipar    
    loader = true;
    moneyOrigin : string = ''
    monthlyAmount: {text: string} ={text: ''};
    counter : number = 3;
    showQuestions = false;
    submitForm = false;
    $refs : any ={}
    accountUses = []
    monthlyAmounts = [];
    localRequirements : ReqLevelTwo; 
    localLevelQuestions = [];

    async created(){
        this.localRequirements = this.requirements;
        await this.fetchQuestions();
        this.localLevelQuestions = this.levelQuestions
        this.accountUses = this.levelQuestions[0].answers; // Cambiar 
        this.monthlyAmounts = this.levelQuestions[1].answers; // Cambiar 
        this.accountUses.forEach(el => {
            if (this.localRequirements.account_uses.value.find(e=> e.id_level_answer === el.id_level_answer))
                el.checked = true
            else el.checked = false
        })
        this.loader = false;
    }

    goBack(){
        this.$emit('prevStep')
    }

    addAccountUses(answer: any){
        if (this.localRequirements.account_uses.rejected !== false){
        if (this.localRequirements.account_uses.value.length >=2){
            if (answer.checked === true){
                answer.checked = false;
                this.localRequirements.account_uses.value =this.localRequirements.account_uses.value.filter(el => el.id_level_answer !== answer.id_level_answer)
            }
            else showToast('Puede seleccionar máximo dos opciones','error')
        }else{
            answer.checked = !answer.checked;
            if (answer.checked) this.localRequirements.account_uses.value.push(answer)
            else this.localRequirements.account_uses.value=this.localRequirements.account_uses.value.filter(el => el.id_level_answer !== answer.id_level_answer)
        }
        this.$forceUpdate();}
    }



    checkSimpleQuestion(answer: Answer){
        this.localRequirements.monthly_amount.value[0]=answer;
        this.$forceUpdate();
    }

    async submit(){
        this.$v.$touch;
        this.submitForm = true;
        if (!this.$v.$invalid && this.localRequirements.monthly_amount.value.length>0){ 
            this.$refs.loader.showLoader();
            this.localLevelQuestions[0].answers = this.localRequirements.account_uses.value;
            this.localLevelQuestions[1].answers = this.localRequirements.monthly_amount.value;
            this.localRequirements.answers = this.localLevelQuestions
            if (!await this.requestLevelTwo(this.localRequirements))
                showToast('Ha ocurrido un error procesando su solicitud','error')
            else {
                showToast('Se ha enviado la solicitud exitosamente','success')
                this.$router.push({name: 'LevelSuccess'})
            }
            this.$refs.loader.hideLoader();
        }
        else showToast('Debe responder todas las preguntas','error')
    }

    @verifLevels.Getter(VerifLevelsMethods.getters.GET_LEVELS_QUESTIONS)
        levelQuestions!: Question[];
    @verifLevels.Action(VerifLevelsMethods.actions.FETCH_LEVELS_QUESTIONS)
        fetchQuestions!: () => Promise<boolean>
    @verifLevels.Getter(VerifLevelsMethods.getters.GET_REQUEST_LEVEL_TWO)
        leveltwoRequirements!: ReqLevelTwo;
    @verifLevels.Action(VerifLevelsMethods.actions.REQUEST_LEVEL_TWO)
        requestLevelTwo!: (requirements: ReqLevelTwo) => Promise<boolean>

}
